.location-container{
    width: 100%;
    height: 35px;
    text-align: left;
    line-height: 35px;
    color:#888;
    overflow: hidden;
}

.location-container h1{
    margin: 0;
    white-space: nowrap;
}

.weather-location-container{
    width: calc(100% - 20px);
    margin-left:10px;
    margin-right: 10px;
    padding: 15px;
    min-width: 350px;
    box-sizing: border-box;
    margin:0 auto;
    font-family: 'Roboto', 'sans-serif';
    box-shadow: 10px 10px 40px -12px rgba(0,0,0,0.75);
    cursor:pointer;
}

.location-container .c-black{
    color:black;
}
.weather-data-container {
  width: 100%;
  height: 100px;
  line-height: 100px;
  margin: 0 auto;
  margin-top: 12px;
  background-color: rgba(200, 200, 200, 0.5);
  border-radius: 5px;
  padding:5px;
  box-sizing: border-box;
}

.weather-extra-info-container {
  width: 40%;
  display: inline-block;
  line-height: 28px;
  text-align: left;
}

.weather-temperature-container {
  width: 60%;
  display: inline-block;
}

.weather-temperature-container i{
  margin-top:15px;
}

.temperature {
  font-size: 28px;
  font-weight: bold;
  padding-left: 20px;
  color: #333;
}

.temperature-type {
  font-size: bold;
  color: #666;
}

.extra-info-text {
  display: block;
  color: #1535c5;
  font-size: 16px;
  font-weight: bold;
}

.wicon {
  color: rgb(255, 72, 0);
}

@media(max-width:64em){
    .location-list{
        max-height: 40vh;
        overflow: auto;
        margin-bottom: 20px;
    }
}

.forecast-title{
    color:white;
    text-align: center;
    padding-top:15px;
    padding-bottom:15px;
}

.location-list{
    height: 80vh;
    overflow: auto;
}
#root{
    text-align: center;
}

.details{
    background: #999999;
    width: calc(100% - 40px);
    height: 80vh;
    overflow: auto;
    padding:0 20px;
}

@media(max-width:64em){
    .detail{
        height: 40vh;
    }
}
